
import { defineComponent, reactive, toRefs, onMounted, provide } from 'vue'

interface dataProps {}
export default defineComponent({
  name: 'tbBreadcrumb',
  props: {
    separator: {
      type: String,
      default: '/',
    },
    separatorClass: {
      type: String,
      default: '',
    },
  },
  setup(props, context) {
    provide('tbBreadcrumb', props)
    const data = reactive({})
    const refData = toRefs(data)

    return { ...refData }
  },
})
